import * as React from 'react';
import { Link } from 'react-router-dom';
import routes from '../../routes';
import './index.scss';
export default function () {
    return React.createElement("div", { className: 'aside' }, buildMenus(routes));
}
const buildMenus = (routes) => {
    const nodes = [];
    routes.forEach((it, index) => {
        if (it.component) {
            nodes.push(React.createElement("li", { key: index, className: 'menu-item' },
                React.createElement(Link, { to: it.path }, it.name)));
        }
        else if (it.children) {
            nodes.push(React.createElement("li", { key: index, className: 'menu-item' },
                React.createElement("span", { className: 'menu-name' }, it.name),
                buildMenus(it.children)));
        }
    });
    return React.createElement("ul", { className: 'menus' }, nodes);
};
