import * as React from 'react';
import * as Loadable from 'react-loadable';
import routes from '../../routes';
import { Route as ReactRoute, Redirect } from 'react-router-dom';
import './index.scss';
const routeComponents = [];
const addRoute = (routes, level = '1') => {
    routes.forEach((it, index) => {
        const key = `${level}_${index}`;
        if (it.component) {
            const componentRoute = it;
            const loadable = Loadable({
                loader: componentRoute.component,
                loading: () => React.createElement("div", null)
            });
            routeComponents.push(React.createElement(ReactRoute, { exact: true, key: key, path: componentRoute.path, component: loadable }));
        }
        else if (it.redirect) {
            const redirectRoute = it;
            routeComponents.push(React.createElement(ReactRoute, { exact: true, key: key, path: redirectRoute.path, component: () => React.createElement(Redirect, { to: redirectRoute.redirect }) }));
        }
        else {
            const childrenRoute = it;
            addRoute(childrenRoute.children, key);
        }
    });
};
addRoute(routes);
export default function () {
    return React.createElement("div", { className: 'content' }, routeComponents);
}
